
@tailwind base;
@tailwind components;
@tailwind utilities;


/* App.css */

.input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto; 
  height: 100vh;
  overflow: auto;

}


.button-holder {
  margin-top: 20px; 
  display: flex;
  justify-content: space-between; 
}


.input-box {
  text-align: center;
  margin-top: 0.5rem;
}

.input-box input {
  width: 100%;
  max-width: 700px; 
  padding: 0.5rem;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}


.input-box input + br {
  margin-top: 0.5rem;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}


.flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}


.text-3xl {
  font-size: 1.875rem; 
  padding-top: 2rem;
}


.pt-10 {
  padding-top: 1.25rem; 
}


.instruction-box {
  max-width: 85%; 
  padding: 1rem;
  border-radius: 15px;
  background-color: #d8f9ff;
  margin-top: 1rem;
  text-align: left;
  font-size: 14px;
}


.instruction-box p {
  line-height: 1.5;
}


.button-holder {
  margin-top: 1.5rem;
}

.prev-button,
.next-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.prev-button {
  background-color: #d3ddff80;
}

.next-button {
  background-color: #71FF90;
  margin-left: 10px;
}


.prev-button:hover,
.next-button:hover {
  background-color: #75f875; 
}


.prev-button:hover {
  background-color: #c1d2fd; 
}

/* Page1.css (or you can include these styles in your existing App.css) */

.page1-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}

.page-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.input-value {
  background-color: #f0f0f0;
  padding: 8px;
  border-radius: 4px;
}

.value-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 20px;
}

.binary-value {
  flex: 1;
  text-align: center;
  width: 33%; 
}

.binary-box {
  background-color: #c0c0c0;
  padding: 10px;
  border-radius: 4px;
}

.divider {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}



.button-holder {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px; /* Adjust the spacing as needed */
}

.circle-container {
  display: flex;
  gap: 10px; /* Adjust the spacing between circles */
}

.circle {
  width: 24px;
  height: 24px;
  background-color:  #eaf0ff; /* Default orange color */
  color: black;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: pointer;

}

.circle.green {
  background-color: #97ffda; /* Green color */
}

.fixed-height-div {
  height: 150px;
  overflow-y: auto;
}

#snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {bottom: 0; opacity: 0;} 
  to {bottom: 30px; opacity: 1;}
}

@keyframes fadein {
  from {bottom: 0; opacity: 0;}
  to {bottom: 30px; opacity: 1;}
}

@-webkit-keyframes fadeout {
  from {bottom: 30px; opacity: 1;} 
  to {bottom: 0; opacity: 0;}
}

@keyframes fadeout {
  from {bottom: 30px; opacity: 1;}
  to {bottom: 0; opacity: 0;}
}

.green #snackbar {
  background-color: green;
}

/* Red Snackbar */
.red #snackbar {
  background-color: red;
}

.disabled {
  opacity: 0.6; /* Reduce opacity to indicate it's disabled */
  cursor: not-allowed; /* Change cursor to indicate it's not clickable */
  pointer-events: none; /* Disable pointer events to prevent interaction */

}
